.header{
    max-width: 100%;
    background-color: #000000;
    height: 110px;
    color: #FFFFFF;
    @include normal;
}

.container_header{
    width: 1170px;
    display: flex;
    left: 300px;

    
    margin-left: auto;
    margin-right: auto;
}
  

.nav_logo{
    height: 110px;
    width: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    @include Perfetto;
    img{
        width: 60px;
        height: 60px;
        margin-top: 15px;
    }
}

#nav_title{
    width: 210px;
    height: 75px;
    align-self: start;
    div:last-of-type{
        left: 25%;
    }
        
}

.nav_items{
    margin-top: 30px;
    width: 900px;
    height: 50px;
}

.wrap{
    width: 630px;
    height: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}



.nav_button{
    width: 110px;
    margin-bottom: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    span{
        border: 1px solid #E05D6B;
        box-shadow: 0px 2px 2px #E05D6B;
        border-radius: 5px;
    }
}
        
    
    
    
    
