

@font-face {
    font-family: 'Roboto' , sans-serif;
    src: url('../fonts/Roboto-Light.ttf') format('truetype');
}
    
@font-face {
    font-family: 'Perfetto' , sans-serif;
    src: url('../fonts/Perfetto.ttf') format('truetype');
}
    


@mixin normal {
    font-family: 'Roboto' , sans-serif;
    font-size: 16px;
    font-weight: 300;
    line-height: 23px;
}
    


@mixin Perfetto {
    font-family: 'Perfetto' , sans-serif;
    font-size: 70px;
}
    
